@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
.sidebar-menu{background-color:#023}.sidebar-menu__nav{display:flex;flex-direction:column;margin-top:100px}.sidebar-menu .nav__item{cursor:pointer;color:#023;background-color:#fff;font-weight:700;text-transform:uppercase;padding:10px 20px;margin-bottom:10px;font-size:12px;transition:background-color;transition-duration:.33s;transition-timing-function:cubic-bezier(0.12, 0.75, 0.4, 1);transition-delay:0s}.sidebar-menu .nav__item:hover{background-color:#cbd3d6}
.alert{padding:10px 20px;border-radius:6px}.alert__error{color:#611a15;background-color:#fdecea}
.button{height:100%;background-color:#023;color:#fff;border:none;border-radius:6px;padding:10px 30px;cursor:pointer;transition:background-color;transition-duration:.33s;transition-timing-function:cubic-bezier(0.12, 0.75, 0.4, 1);transition-delay:0s}.button:hover{background-color:#000}
.select-input{cursor:pointer;display:flex;flex-direction:column;position:relative;border:1px solid #023;padding:10px 20px;border-radius:6px;width:250px}.select-input__selected{display:flex;align-items:center;justify-content:space-between}.select-input:hover{color:#fff;background-color:#023}.select-input:hover .icon path{fill:#fff}.select-input:hover::after{background-color:#023}.select-input::after{content:"";width:100%;height:10px;background-color:#fff;position:absolute;left:0;right:0;bottom:3px}.select-input__options{width:calc(100% + 2px);overflow:hidden;position:absolute;background-color:#f3f4f5;border:1px solid #023;border-radius:6px;transform:translate(-21px, 26px);display:none}.select-input__options--opened{display:block}.select-input__options .option{padding:10px 20px;color:#023;transition:background-color;transition-duration:.33s;transition-timing-function:cubic-bezier(0.12, 0.75, 0.4, 1);transition-delay:0s}.select-input__options .option:hover{color:#fff;background-color:#023}.select-input__options .option--pressed{color:#023;background-color:#e5e9ea}
.text-input{background-color:#f3f4f5;border:1px solid #023;border-radius:6px;padding:10px 10px;color:#023}
.listing-header{display:flex;justify-content:space-between;flex-wrap:wrap;height:150px;align-items:center}.listing-header__search,.listing-header__sorting{width:100%}@media only screen and (min-width: 1024px){.listing-header__search,.listing-header__sorting{width:50%}}.listing-header .search__button{margin-left:20px;width:120px}.listing-header .search__input{width:calc(100% - 140px)}.listing-header__sorting{display:flex;align-items:center;justify-content:flex-end;flex-wrap:wrap}.listing-header__sorting .label{margin-right:20px}
.tag{border-radius:6px;background-color:#ebf0f2;color:#023;font-size:10px;font-weight:bolder;margin-right:10px;margin-bottom:5px;padding:2px 10px}
.details{padding:20px;display:flex;flex-direction:column;height:100%;border-bottom:4px solid #d6e0e4}.details__title{font-size:20px;color:#33667e}.details__tags{display:flex;flex-wrap:wrap}.details__footer{display:flex;align-items:flex-end;justify-content:space-between;font-size:12px;font-weight:bold;height:100%;margin-top:40px}.details__footer .code{color:#d6e0e4}.details__footer .location{display:flex;align-items:flex-end;color:#33667e}
.list-item{box-shadow:3px 3px 22px 0 rgba(0,0,0,.14);display:flex;flex-direction:column;background-color:#fff;border-radius:6px;overflow:hidden}.list-item .item__image{min-height:300px;height:300px;width:100%;object-fit:cover}.list-item .item__price{padding:20px 20px 10px;display:flex;flex-direction:column;font-size:12px;color:#33667e}.list-item .item__price .ask-price,.list-item .item__price .current-offer{display:flex;justify-content:space-between;width:100%}.list-item .item__price .ask-price{margin-bottom:10px;font-weight:lighter}.list-item .item__price .ask-price__value{font-size:18px}.list-item .item__price .current-offer{font-weight:bolder}.list-item .item__price .current-offer__value{font-size:18px}
.listing-page{margin:0 40px;overflow:scroll;max-height:100vh}.listing-page .list{display:grid;grid-gap:20px}@media only screen and (min-width: 1024px){.listing-page .list{grid-template-columns:repeat(3, 1fr)}}@media screen and (min-device-width: 768px)and (max-device-width: 1024px){.listing-page .list{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-device-width: 640px){.listing-page .list{grid-template-columns:repeat(1, 1fr)}}
.app{display:grid;grid-template-columns:-webkit-min-content auto;grid-template-columns:min-content auto;grid-template-rows:1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:". .";height:100%}
html,body{font-family:"Montserrat",sans-serif}#root{height:100vh}*,*::before,*::after{box-sizing:border-box}ul[class],ol[class]{padding:0}body,h1,h2,h3,h4,p,ul[class],ol[class],li,figure,figcaption,blockquote,dl,dd{margin:0}body{min-height:100vh;scroll-behavior:smooth;text-rendering:optimizeSpeed;line-height:1.5}ul[class],ol[class]{list-style:none}a:not([class]){-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto}img{max-width:100%;display:block}article>*+*{margin-top:1em}input,button,textarea,select{font-family:inherit;font-size:inherit;font-style:inherit;font-weight:inherit}@media(prefers-reduced-motion: reduce){*{animation-duration:.01ms !important;animation-iteration-count:1 !important;transition-duration:.01ms !important;scroll-behavior:auto !important}}
